export default {
	data() {
		return {
			index: null,
			picker: ['喵喵喵', '汪汪汪', '哼唧哼唧'],
			multiIndex: [0, 0, 0],
			multiArray: [
				['无脊柱动物', '脊柱动物'],
				['扁性动物', '线形动物', '环节动物', '软体动物', '节肢动物'],
				['猪肉绦虫', '吸血虫']
			],
			time: '12:00',
			date: '2020-03-02',
			region: ['河北省', '廊坊市', '固安县'],
			imgList: [],
			modalName: null,
			textareaAValue: '',
			textareaBValue: ''
		}
	},
	methods: {
		pickerChange(e) {
			let that = this
			that.index = e.detail.value
		},
		multiChange(e) {
			let that = this
			that.multiIndex = e.detail.value
		},
		multiColumnChange(e) {
			let that = this
			let data = {
				multiArray: that.multiArray,
				multiIndex: that.multiIndex
			};
			data.multiIndex[e.detail.column] = e.detail.value;
			switch (e.detail.column) {
				case 0:
					switch (data.multiIndex[0]) {
						case 0:
							data.multiArray[1] = ['扁性动物', '线形动物', '环节动物', '软体动物', '节肢动物'];
							data.multiArray[2] = ['猪肉绦虫', '吸血虫'];
							break;
						case 1:
							data.multiArray[1] = ['鱼', '两栖动物', '爬行动物'];
							data.multiArray[2] = ['鲫鱼', '带鱼'];
							break;
					}
					data.multiIndex.splice(1, 1, 0)
					data.multiIndex.splice(2, 1, 0)
					break;
				case 1:
					switch (data.multiIndex[0]) {
						case 0:
							switch (data.multiIndex[1]) {
								case 0:
									data.multiArray[2] = ['猪肉绦虫', '吸血虫'];
									break;
								case 1:
									data.multiArray[2] = ['蛔虫'];
									break;
								case 2:
									data.multiArray[2] = ['蚂蚁', '蚂蟥'];
									break;
								case 3:
									data.multiArray[2] = ['河蚌', '蜗牛', '蛞蝓'];
									break;
								case 4:
									data.multiArray[2] = ['昆虫', '甲壳动物', '蛛形动物', '多足动物'];
									break;
							}
							break;
						case 1:
							switch (data.multiIndex[1]) {
								case 0:
									data.multiArray[2] = ['鲫鱼', '带鱼'];
									break;
								case 1:
									data.multiArray[2] = ['青蛙', '娃娃鱼'];
									break;
								case 2:
									data.multiArray[2] = ['蜥蜴', '龟', '壁虎'];
									break;
							}
							break;
					}
					data.multiIndex.splice(2, 1, 0)
					break;
			}
			that.multiArray = data.multiArray
			that.multiIndex = data.multiIndex
		},
		timeChange(e) {
			let that = this
			that.time = e.detail.value
		},
		dateChange(e) {
			let that = this
			that.date = e.detail.value
		},
		regionChange(e) {
			let that = this
			that.region = e.detail.value
		},
		chooseImg() {
			let that = this
			uni.chooseImage({
				count: 4, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: function(res) {
					if (that.imgList.length != 0) {
						that.imgList = that.imgList.concat(res.tempFilePaths)
					} else {
						that.imgList = res.tempFilePaths
					}
				}
			});
		},
		viewImage(e) {
			let that = this
			console.log(e.currentTarget.dataset.url)
			uni.previewImage({
				urls: that.imgList,
				current: e.currentTarget.dataset.url,
				longPressActions: {
					itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function(data) {
						console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				}
			})
		},
		delImg(e) {
			let that = this
			uni.showModal({
				title: '召唤师',
				content: '确定要删除这段回忆吗？',
				cancelText: '再看看',
				confirmText: '再见',
				success: res => {
					if (res.confirm) {
						that.imgList.splice(e.currentTarget.dataset.index, 1);
					}
				}
			})
		},
		textareaAInput(e) {
			let that = this
			that.textareaAValue = e.detail.value
		},
		textareaBInput(e) {
			let that = this
			that.textareaBValue = e.detail.value
		}
	}
}
